<template>
	<div id="task">
		<div class="memorandum">
			<div style="height: 110px;">
				<h1>17备忘录系统</h1>
				<div class="username">登录名:<span>{{username}}</span> <a href="" @click.prevent="logout()">退出</a></div>
			</div>
			<div class="task">
				<input type="text" placeholder="任务标题" v-model="title">
				<button type="button" @click="addInfo()">添加</button>
				<button type="button" @click="clearInfo()">清空备忘录</button>
				<ul class="swa" style="width: 100%; height: 60px; background-color: #d4d5d7;">
					<li style="width: 5%;">编号</li>
					<li style="width: 55%;">标题</li>
					<li style="width: 14%;">状态</li>
					<li style="width: 25%;">操作</li>
				</ul>
				<ul class="was" v-for="(item,index) in events" :key="index">
					<span style="width: 5%;" ref="event">{{item.id}}</span>
					<span style="width: 55%; text-align: left;">{{item.title}}</span>
					<span style="width: 14%;">{{item.is_complete == 0? '未完成' : '已完成'}}</span>
					<span style="width: 25%;">
						<a href="" @click.prevent="eventInfo(index)" v-if="item.is_complete==0">关闭任务</a>
						<a href="" @click.prevent="againInfo(index)" v-else>重启任务</a>&nbsp;
						<a href="" @click.prevent="deleteInfo(index)">删除</a>
					</span>
				</ul>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				username: window.localStorage.getItem('username'),
				user_id: window.localStorage.getItem('user_id'),
				events: [],
				title: "",
				password: '',
				isStatus: false,
			}
		},
		mounted() {
			this.getInfo();
		},
		methods: {
			logout() {
				this.$store.dispatch('logout')
				this.$router.push('/login')
			},
			addInfo: function() {
				var that = this;
				let params = {
					user_id: that.user_id,
					title: that.title
				};
				this.$http.post(this.$host + '/info/create', params)
					.then(function(response) {
						let result = response.data;
						if (result.status == 200) {
							that.getInfo()
						} else {
							alert(result.message)
						}
					})
					.catch(function(error) {
						console.log(error)
					});
			},
			clearInfo: function() {
				var that = this;
				let params = {
					user_id: that.user_id,
				};
				this.$http.post(this.$host + '/info/clear', params)
					.then(function(response) {
						let result = response.data;
						if (result.status == 200) {
							that.getInfo()
						} else {
							alert(result.message)
						}
					})
					.catch(function(error) {
						console.log(error)
					});
			},
			deleteInfo: function(index) {
				var that = this;
				let params = {
					user_id: that.user_id,
					note_id: that.events[index].id
				};
				this.$http.post(this.$host + '/info/delete', params)
					.then(function(response) {
						let result = response.data;
						if (result.status == 200) {
							that.getInfo()
						} else {
							alert(result.message)
						}
					})
					.catch(function(error) {
						console.log(error)
					});
			},
			eventInfo: function(index) {
				var that = this;
				let params = {
					user_id: that.user_id,
					note_id: that.events[index].id
				};
				this.$http.post(this.$host + '/info/change', params)
					.then(function(response) {
						let result = response.data;
						if (result.status == 200) {
							that.getInfo()
						} else {
							alert(result.message)
						}
					})
					.catch(function(error) {
						console.log(error)
					});
			},
			againInfo: function(index) {
				var that = this;
				let params = {
					user_id: that.user_id,
					note_id: that.events[index].id
				};
				this.$http.post(this.$host + '/info/change', params)
					.then(function(response) {
						let result = response.data;
						if (result.status == 200) {
							that.getInfo()
						} else {
							alert(result.message)
						}
					})
					.catch(function(error) {
						console.log(error)
					});
			},
			getInfo() {
				let that = this
				this.$http.get(this.$host + '/info/lists?user_id=' + that.user_id)
					.then(function(response) {
						let result = response.data;
						if (result.status == 200) {
							that.events = result.data;
						}
					})
					.catch(function(error) {
						console.log(error)
					});
			}
		}
	}
</script>

<style scoped>
	.memorandum h1 {
		font-size: 40px;
		color: #808080;
	}

	.username {
		float: right;
		font-size: 20px;
		margin-right: 10px;
	}

	.username span {
		color: red;
		margin: 0 15px;
	}

	.task {
		display: block;
	}

	.task input {
		width: 400px;
		height: 40px;
		box-sizing: border-box;
		padding: 5px;
	}

	.task button {
		background-color: darkcyan;
		height: 40px;
		padding: 5px;
		margin: 0 5px;
		color: #fff;
		cursor: pointer;
	}

	ul,
	li {
		margin: 0;
		padding: 0;
		list-style: none;
	}

	.swa li {
		display: inline-block;
		height: 60px;
		text-align: center;
		line-height: 60px;
	}

	.was {
		border-bottom: solid 1px;
	}

	.was span {
		height: 60px;
		display: inline-block;
		text-align: center;
		line-height: 60px;
	}
</style>
