import Vue from 'vue'
import VueRouter from 'vue-router'
import Task from '../views/Task.vue'
import Login from '../views/Login.vue'

Vue.use(VueRouter)

const routes = [
	{
		path: '/',
		name: 'Task',
		component: Task,
		meta: {
			isAuth:true
		}
	},
	{
		path: '/task',
		name: 'Task',
		component: Task,
		meta: {
			isAuth:true
		}
	},
	{
		path: '/login',
		name: 'Login',
		component: Login
	}
]

const router = new VueRouter({
	mode:'history',
	routes
})

router.beforeEach((to, from, next) => {
	if (to.meta.isAuth) {
		if(!window.localStorage.getItem('username')){
			next('/login')
		}else{
			next()
		}
	}else {
		next()
	}
})

export default router
