<template>
	<div class="login">
		<span><b>登录界面</b></span>
		<input type="text" v-model="username" placeholder="请输入用户名" />
		<input type="password"  v-model="password" placeholder="请输入密码"/>
		<button type="button" @click="login()">登录</button>
	</div>
</template>

<script>
	export default {
		data() {
			return{
				username:'',
				password:''
			}
		},
		methods:{
			login() {
				let params = {
					username: this.username,
					password: this.password,
					user_id: null
				};
				this.$http.post(this.$host + '/user/login', params)
					.then(response => {
						let result = response.data
						console.log(result)
						if (result.status == 200) {
							this.$store.dispatch('login', {username: result.data.realname,user_id: result.data.id})
							this.$router.push('/task')
						} else{
							alert(result.message)
						}
					})
					.catch(error => {
						console.log(error)
					})
			}
		}
	}
</script>

<style>
	.login{
		width: 800px;
		height: 500px;
		margin: 100px auto;
	}
	.login span{
		text-align: center;
		color: darkcyan;
		font-size: 30px;
		display: block;
		margin: 0 auto;
	}
	.login input{
		width: 800px;
		height: 50px;
		padding: 5px;
		margin-top: 40px;
		border-radius: 10px;
	}
	.login button{
		width: 810px;
		height: 60px;
		color: #FFFFFF;
		text-align: center;
		line-height: 60px;
		margin-top: 40px;
		background-color: darkcyan;
		border-radius: 10px;
		cursor: pointer;
	}
</style>
